import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getContacts } from "../../redux/app/contact-selector";
import contactsOperations from "../../redux/app/contact-operations";
import shortid from "shortid";
import s from "./ContactForm.module.css";

export default function ContactForm() {
  const contacts = useSelector(getContacts);
  const dispatch = useDispatch();

  const contactNameId = shortid.generate();
  const contactNumberId = shortid.generate();

  const [name, setName] = useState("");
  const [number, setNumber] = useState("");

  const handleChange = (e) => {
    const { name, value } = e.currentTarget;
    switch (name) {
      case "name":
        setName(value);
        break;
      case "number":
        setNumber(value);
        break;
      default:
        return;
    }
  };
  // –ú–µ—Ç–æ–¥ –Ω–∞ –æ—Ç–ø—Ä–∞–≤–∫–µ —Ñ–æ—Ä–º—ã. –§–æ—Ä–º–∏—Ä—É–µ—Ç –∏–∑ —Å—Ç–µ–π—Ç–∞ –∫–æ–Ω—Ç–∞–∫—Ç –∏ –ø–µ—Ä–µ–¥–∞–µ—Ç –≤–æ –≤–Ω–µ—à–Ω–∏–π –º–µ—Ç–æ–¥
  const handleSubmit = (e) => {
    e.preventDefault();

    if (name === "") {
      alert(`Put name, please`);
      return;
    }

    if (number === "") {
      alert(`Put number, please`);
    }

    if (contacts.find((contact) => contact.name === name)) {
      alert(`${name} is already in contacts!üôÑ`);
      reset();
      return;
    }
    dispatch(contactsOperations.addContact(name, number));
    reset();
  };
  const reset = () => {
    setName("");
    setNumber("");
  };

  return (
    <form className={s.form} onSubmit={handleSubmit}>
      <label className={s.label} htmlFor={contactNameId}>
        Name
        <input
          type="text"
          name="name"
          placeholder="Contact name"
          aria-label="input to you name"
          className={s.input}
          value={name}
          onChange={handleChange}
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
          id={contactNameId}
        />
      </label>
      <label className={s.label} htmlFor={contactNumberId}>
        Number
        <input
          type="tel"
          name="number"
          placeholder="Phone number"
          aria-label="input to you phone number"
          className={s.input}
          value={number}
          onChange={handleChange}
          pattern="(\+?( |-|\.)?\d{1,2}( |-|\.)?)?(\(?\d{3}\)?|\d{3})( |-|\.)?(\d{3}( |-|\.)?\d{4})"
          title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –∏–∑ 11-12 —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ü–∏—Ñ—Ä—ã, –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –ø—É–∑–∞—Ç—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
          id={contactNumberId}
        />
      </label>
      <div className={s.button__wrapper}>
        <button type="submit" className={s.button}>
          Add contact
        </button>
      </div>
    </form>
  );
}
